"use client";

import React, { useState } from 'react';

interface Activity {
  id: number;
  title: string;
  description: string;
  category: string;
  date: string;
  location: string;
  price: string;
  organizer: string;
  status: 'pending' | 'approved' | 'rejected';
}

export default function ActivityHub() {
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    category: '',
    date: '',
    location: '',
    price: '',
    organizer: ''
  });

  const [activities, setActivities] = useState<Activity[]>([
    {
      id: 1,
      title: "Workshop Gốm Sứ Bát Tràng",
      description: "Học nghệ thuật làm gốm truyền thống với nghệ nhân làng Bát Tràng",
      category: "workshop",
      date: "2024-02-15",
      location: "Làng Bát Tràng, Hà Nội",
      price: "300,000 VND",
      organizer: "Nghệ nhân Nguyễn Văn A",
      status: 'approved'
    },
    {
      id: 2,
      title: "Trải nghiệm AR Áo Dài",
      description: "Thử nghiệm công nghệ AR để thiết kế và mặc thử áo dài truyền thống",
      category: "ar-experience",
      date: "2024-02-20",
      location: "Bảo tàng Phụ nữ Việt Nam",
      price: "150,000 VND",
      organizer: "ArtTech Vietnam",
      status: 'approved'
    },
    {
      id: 3,
      title: "Lễ hội Đờn Ca Tài Tử",
      description: "Sự kiện âm nhạc dân gian với projection mapping 3D",
      category: "festival",
      date: "2024-03-01",
      location: "TP. Hồ Chí Minh",
      price: "Free",
      organizer: "Sở Văn hóa TPHCM",
      status: 'pending'
    }
  ]);

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    if (!formData.title || !formData.description || !formData.category) {
      setError('Vui lòng điền đầy đủ thông tin bắt buộc');
      return;
    }

    const newActivity: Activity = {
      id: activities.length + 1,
      ...formData,
      status: 'pending'
    };

    setActivities([newActivity, ...activities]);
    setSuccess('Đề xuất hoạt động đã được gửi thành công!');
    setFormData({
      title: '',
      description: '',
      category: '',
      date: '',
      location: '',
      price: '',
      organizer: ''
    });
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'approved': return 'text-green-600 bg-green-100';
      case 'rejected': return 'text-red-600 bg-red-100';
      default: return 'text-yellow-600 bg-yellow-100';
    }
  };

  const getStatusText = (status: string) => {
    switch (status) {
      case 'approved': return 'Đã duyệt';
      case 'rejected': return 'Từ chối';
      default: return 'Chờ duyệt';
    }
  };

  return (
    <div className="activity-hub border border-gray-300 rounded p-6">
      <h2 className="text-2xl font-semibold mb-4">Activity Hub – Trung tâm Hoạt động Di sản</h2>
      
      {/* Submission Form */}
      <div className="bg-gray-50 rounded p-4 mb-6">
        <h3 className="text-lg font-semibold mb-3">Đăng tải đề xuất hoạt động mới</h3>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <input
              type="text"
              name="title"
              value={formData.title}
              onChange={handleInputChange}
              placeholder="Tiêu đề đề xuất *"
              required
              className="border border-gray-300 rounded px-3 py-2"
            />
            <select
              name="category"
              value={formData.category}
              onChange={handleInputChange}
              required
              className="border border-gray-300 rounded px-3 py-2"
            >
              <option value="">Chọn loại hoạt động *</option>
              <option value="workshop">Workshop</option>
              <option value="festival">Lễ hội</option>
              <option value="ar-experience">Trải nghiệm AR/VR</option>
              <option value="exhibition">Triển lãm</option>
              <option value="performance">Biểu diễn</option>
            </select>
          </div>
          
          <textarea
            name="description"
            value={formData.description}
            onChange={handleInputChange}
            placeholder="Nội dung chi tiết *"
            required
            className="w-full border border-gray-300 rounded px-3 py-2"
            rows={3}
          ></textarea>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <input
              type="date"
              name="date"
              value={formData.date}
              onChange={handleInputChange}
              className="border border-gray-300 rounded px-3 py-2"
            />
            <input
              type="text"
              name="location"
              value={formData.location}
              onChange={handleInputChange}
              placeholder="Địa điểm"
              className="border border-gray-300 rounded px-3 py-2"
            />
            <input
              type="text"
              name="price"
              value={formData.price}
              onChange={handleInputChange}
              placeholder="Chi phí (VD: 200,000 VND)"
              className="border border-gray-300 rounded px-3 py-2"
            />
          </div>
          
          <input
            type="text"
            name="organizer"
            value={formData.organizer}
            onChange={handleInputChange}
            placeholder="Tên tổ chức/nghệ nhân"
            className="w-full border border-gray-300 rounded px-3 py-2"
          />
          
          <button
            type="submit"
            className="bg-red-800 text-white px-6 py-2 rounded hover:bg-red-900 transition"
          >
            Đăng tải đề xuất
          </button>
        </form>
        
        {error && <p className="text-red-600 mt-2">{error}</p>}
        {success && <p className="text-green-600 mt-2">{success}</p>}
      </div>

      {/* Activities List */}
      <div className="activity-list">
        <h3 className="text-lg font-semibold mb-4">Danh sách hoạt động ({activities.length})</h3>
        <div className="space-y-4">
          {activities.map((activity) => (
            <div key={activity.id} className="border border-gray-200 rounded p-4 hover:shadow-md transition-shadow">
              <div className="flex justify-between items-start mb-2">
                <h4 className="text-lg font-semibold text-red-800">{activity.title}</h4>
                <span className={`px-2 py-1 rounded text-xs font-medium ${getStatusColor(activity.status)}`}>
                  {getStatusText(activity.status)}
                </span>
              </div>
              <p className="text-gray-600 mb-2">{activity.description}</p>
              <div className="grid grid-cols-2 md:grid-cols-4 gap-2 text-sm text-gray-500">
                <div><strong>Ngày:</strong> {activity.date}</div>
                <div><strong>Địa điểm:</strong> {activity.location}</div>
                <div><strong>Chi phí:</strong> {activity.price}</div>
                <div><strong>Tổ chức:</strong> {activity.organizer}</div>
              </div>
              {activity.status === 'approved' && (
                <button className="mt-3 bg-green-600 text-white px-4 py-1 rounded text-sm hover:bg-green-700 transition">
                  Đăng ký tham gia
                </button>
              )}
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
