"use client";

import React, { useState } from 'react';

interface Location {
  id: number;
  name: string;
  category: string;
  description: string;
  priceRange: string;
  distance: string;
  coords: [number, number];
}

const sampleLocations: Location[] = [
  {
    id: 1,
    name: "Đoàn Lô Tô Sài Gòn Tân Thời",
    category: "performance",
    description: "Tụ điểm lô tô lớn nhất Sài Gòn hiện nay, tổ chức biểu diễn cố định vào cuối tuần với sức chứa 200–300 khách, miễn phí vé vào cửa, không gian thân thiện LGBTQ+ và có chất lượng nghệ thuật, âm thanh – ánh sáng nổi bật trên nền văn hóa dân gian Nam Bộ.",
    priceRange: "Free",
    distance: "Quận 1, TP.HCM",
    coords: [10.7769, 106.7009]
  },
  {
    id: 2,
    name: "Hòa Bình District 10 Cultural Center",
    category: "cultural-center",
    description: "Thường xuyên đăng cai các chương trình lô tô từ Đoàn Sài Gòn Tân Thời, tạo không gian mở đẹp, rộng và hiện đại, thuận tiện cho các sự kiện cộng đồng và nghệ thuật dân gian.",
    priceRange: "Free - 50,000 VND",
    distance: "Quận 10, TP.HCM",
    coords: [10.7640, 106.6677]
  },
  {
    id: 3,
    name: "Gánh Hát Lô Tô Hương Nam",
    category: "performance",
    description: "Đoàn trẻ năng động của nghệ sĩ Su Su tổ chức đều đặn tại các sự kiện, sân khấu hoặc trong các dịp lễ hội ở Quận 11, giúp phục dựng không gian lô tô truyền thống với nhạc dân gian và hoạt động tương tác đặc sắc.",
    priceRange: "100,000 - 200,000 VND",
    distance: "Quận 11, TP.HCM",
    coords: [10.7625, 106.6509]
  },
  {
    id: 4,
    name: "Bến Bình Đông - Đờn ca tài tử",
    category: "traditional-music",
    description: "Không gian bên sông nước, một trong hơn 290 câu lạc bộ đờn ca tài tử tại TP.HCM, nơi thực hành nghệ thuật truyền thống Nam Bộ với không gian tự nhiên đẹp.",
    priceRange: "Free",
    distance: "Quận 8, TP.HCM",
    coords: [10.7378, 106.6621]
  },
  {
    id: 5,
    name: "Lăng Ông Thủy Tướng - Lễ hội Nghinh Ông",
    category: "festival",
    description: "Sự kiện văn hóa lớn tổ chức mỗi năm tại di tích Lăng Ông Thủy Tướng, bao gồm phần lễ và hội với trình diễn ngư nghiệp, thể thao truyền thống, triển lãm nghề biển, múa lân sư rồng và hội hoa đăng rất đặc sắc.",
    priceRange: "Free",
    distance: "Huyện Cần Giờ, TP.HCM",
    coords: [10.4167, 106.9500]
  },
  {
    id: 6,
    name: "Lăng Đức Tả quân Lê Văn Duyệt",
    category: "historical-site",
    description: "Di tích lịch sử quốc gia tổ chức Lễ hội Khai hạ – Cầu an vào mùng 7 Tết Âm lịch hàng năm với các nghi lễ tế lễ cung đình triều Nguyễn, các nghi thức khai hạ, cầu an, khai ấn, khai bút.",
    priceRange: "Free",
    distance: "Quận Bình Thạnh, TP.HCM",
    coords: [10.8142, 106.7317]
  }
];

export default function HeritageMapExplorer() {
  const [selectedCategory, setSelectedCategory] = useState('');
  const [searchKeyword, setSearchKeyword] = useState('');
  const [selectedLocation, setSelectedLocation] = useState<Location | null>(null);

  const filteredLocations = sampleLocations.filter(location => {
    const matchesCategory = !selectedCategory || location.category === selectedCategory;
    const matchesKeyword = !searchKeyword || 
      location.name.toLowerCase().includes(searchKeyword.toLowerCase()) ||
      location.description.toLowerCase().includes(searchKeyword.toLowerCase());
    return matchesCategory && matchesKeyword;
  });

  return (
    <div className="heritage-map-explorer border border-gray-300 rounded p-6">
      <h2 className="text-2xl font-semibold mb-4">Heritage Map Explorer</h2>
      
      {/* Search and Filter Controls */}
      <div className="controls mb-6 space-y-4">
        <input
          type="text"
          placeholder="Tìm kiếm theo từ khóa..."
          value={searchKeyword}
          onChange={(e) => setSearchKeyword(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        />
        <select
          value={selectedCategory}
          onChange={(e) => setSelectedCategory(e.target.value)}
          className="w-full border border-gray-300 rounded px-3 py-2"
        >
          <option value="">Tất cả danh mục</option>
          <option value="performance">Biểu diễn Lô Tô</option>
          <option value="cultural-center">Trung tâm Văn hóa</option>
          <option value="traditional-music">Đờn ca tài tử</option>
          <option value="festival">Lễ hội</option>
          <option value="historical-site">Di tích lịch sử</option>
        </select>
      </div>

      {/* Google Maps Container */}
      <div className="map-container relative rounded-lg h-96 mb-6 overflow-hidden border-2 border-gray-300">
        <iframe
          src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d125405.90332547!2d106.58493!3d10.762622!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31752ec3c161a3fb%3A0xef77cd47a1cc691e!2sHo%20Chi%20Minh%20City%2C%20Vietnam!5e0!3m2!1sen!2s!4v1635789012345!5m2!1sen!2s"
          width="100%"
          height="100%"
          style={{ border: 0 }}
          allowFullScreen
          loading="lazy"
          referrerPolicy="no-referrer-when-downgrade"
          title="Bản đồ Di sản Sài Gòn"
        ></iframe>
        
        {/* Map Overlay with Heritage Locations */}
        <div className="absolute inset-0 pointer-events-none">
          {/* Heritage Location Markers Overlay */}
          {filteredLocations.map((location, index) => {
            const positions = [
              { left: '25%', top: '40%' }, // Quận 1 - Đoàn Lô Tô Sài Gòn
              { left: '45%', top: '35%' }, // Quận 10 - Hòa Bình Cultural Center
              { left: '55%', top: '45%' }, // Quận 11 - Gánh Hát Lô Tô
              { left: '30%', top: '60%' }, // Quận 8 - Bến Bình Đông
              { left: '70%', top: '80%' }, // Cần Giờ - Lăng Ông
              { left: '65%', top: '50%' }  // Bình Thạnh - Lăng Lê Văn Duyệt
            ];
            const pos = positions[index] || { left: '50%', top: '50%' };
            
            return (
              <div
                key={location.id}
                className={`absolute w-6 h-6 rounded-full cursor-pointer transform hover:scale-125 transition-transform border-2 border-white shadow-lg pointer-events-auto ${
                  location.category === 'performance' ? 'bg-red-500' :
                  location.category === 'cultural-center' ? 'bg-blue-500' :
                  location.category === 'traditional-music' ? 'bg-green-500' :
                  location.category === 'festival' ? 'bg-yellow-500' :
                  location.category === 'historical-site' ? 'bg-purple-500' : 'bg-gray-500'
                }`}
                style={{
                  left: pos.left,
                  top: pos.top,
                  transform: 'translate(-50%, -50%)'
                }}
                onClick={() => setSelectedLocation(location)}
              />
            );
          })}
        </div>
        
        {/* Map Title */}
        <div className="absolute top-4 left-4 bg-white px-3 py-1 rounded shadow-md pointer-events-none">
          <span className="text-sm font-semibold text-gray-700">Bản đồ Di sản Sài Gòn</span>
        </div>
        
        {/* Legend */}
        <div className="absolute bottom-4 right-4 bg-white p-3 rounded shadow-md pointer-events-none">
          <div className="text-xs font-semibold mb-2">Chú thích:</div>
          <div className="space-y-1 text-xs">
            <div className="flex items-center"><div className="w-3 h-3 bg-red-500 rounded-full mr-2"></div>Biểu diễn</div>
            <div className="flex items-center"><div className="w-3 h-3 bg-blue-500 rounded-full mr-2"></div>Văn hóa</div>
            <div className="flex items-center"><div className="w-3 h-3 bg-green-500 rounded-full mr-2"></div>Âm nhạc</div>
            <div className="flex items-center"><div className="w-3 h-3 bg-yellow-500 rounded-full mr-2"></div>Lễ hội</div>
            <div className="flex items-center"><div className="w-3 h-3 bg-purple-500 rounded-full mr-2"></div>Di tích</div>
          </div>
        </div>
      </div>

      {/* Location Details Popup */}
      {selectedLocation && (
        <div className="location-popup border border-gray-300 rounded p-4 bg-white shadow-lg">
          <div className="flex justify-between items-start mb-2">
            <h3 className="text-xl font-semibold">{selectedLocation.name}</h3>
            <button
              onClick={() => setSelectedLocation(null)}
              className="text-gray-500 hover:text-gray-700"
            >
              ✕
            </button>
          </div>
          <p className="text-gray-600 mb-2">{selectedLocation.description}</p>
          <div className="details space-y-1 text-sm">
            <p><strong>Khoảng cách:</strong> {selectedLocation.distance}</p>
            <p><strong>Giá:</strong> {selectedLocation.priceRange}</p>
          </div>
          <button className="mt-4 bg-red-800 text-white px-4 py-2 rounded hover:bg-red-900 transition">
            Book ngay
          </button>
        </div>
      )}

      {/* Location List */}
      <div className="location-list">
        <h3 className="text-lg font-semibold mb-3">Danh sách địa điểm ({filteredLocations.length})</h3>
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {filteredLocations.map((location) => (
            <div
              key={location.id}
              className="location-card border border-gray-200 rounded p-3 cursor-pointer hover:shadow-md transition-shadow"
              onClick={() => setSelectedLocation(location)}
            >
              <h4 className="font-semibold">{location.name}</h4>
              <p className="text-sm text-gray-600 mb-2">{location.description}</p>
              <div className="text-xs text-gray-500">
                <p>{location.distance}</p>
                <p>{location.priceRange}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
